import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { environment } from '../../environments/environment';

import 'rxjs/add/operator/map';

@Injectable()
export class WebService {

  public trainers_arr = null;
  public selected_date = null;
  public selected_month = null;
  public selected_month_index = null;
  public selected_year = null;
  public selected_trainer_key = null;
  public selected_trainer_name = null;
  public trainer_schedule = null;
  public selected_slot = null;
  public baseURL: string = '';

  public environment_object = null;


  public submissionResponse = null;

  constructor(private _http: Http) {
    this.environment_object = environment;
    this.baseURL = this.environment_object['base_url'];
  }

  selectTrainerCall(trainer) {
    return "Saved trainer " + trainer;
  }

  getTrainers() {
    return this._http.get(this.baseURL + '/getTrainers')
      .map(res => res);
      
  }

  getTrainerScheduleTillEOM(trainer_key, month) {
    return this._http.get(this.baseURL + '/get_schedule_till_end_of_month2?trainer_key=' + trainer_key + '&month=' + month)
      .map(res => res);
  }

  submitAnAppointment(appointmentObj) {
    return this._http.post(this.baseURL + '/submitAppointment', appointmentObj)
      .map(res => res);
  }
}



